import 'experience/Experience.css';

import React from 'react';
import { Breadcrumb, Grid, Row, Col, PageHeader } from 'react-bootstrap';
import { FormattedMessage, FormattedHTMLMessage, injectIntl, intlShape } from 'react-intl';
import TableOfContents from 'common/table-of-contents/TableOfContents';
import ExperienceItem from './ExperienceItem';

class Experience extends React.Component {

  componentDidMount() {
    document.title = this.props.intl.formatMessage({id: "experience.header"}) + " - Andres Muñoz";
  }

  render() {
    const formatMessage = this.props.intl.formatMessage;

    return (
      <Grid>

        {/* Breadcrumb */}
        <Row>
          <Col xs={12}>
            <Breadcrumb>
              <Breadcrumb.Item href="/"><FormattedMessage id="home.breadcrumb"/></Breadcrumb.Item>
              <Breadcrumb.Item active><FormattedMessage id="experience.breadcrumb"/></Breadcrumb.Item>
            </Breadcrumb>
          </Col>
        </Row>

        {/* Introduction */}
        <Row>
          <Col xs={12}>
            <PageHeader><FormattedMessage id="experience.header"/></PageHeader>
            <p><FormattedHTMLMessage id="experience.introduction"/></p>
          </Col>
        </Row>

        <TableOfContents items={[
          { hash: 'beitech', name: 'Beitech' },
          { hash: 'zenware', name: 'Zenware' },
          { hash: 'avantel', name: 'Avantel' },
          { hash: 'soluciones360', name: 'Soluciones 360' },
          { hash: 'envasadorasycentrifugas', name: 'Envasadoras y Centrifugas' }
        ]}/>

        <ExperienceItem id="beitech" header="Beitech" website="http://beitech.co"
            jobTitle={formatMessage({id: 'experience.beitech.jobTitle'})}
            dateRange={formatMessage({id: 'experience.beitech.dateRange'})}
            details={[
              formatMessage({id: 'experience.beitech.details0'}),
              formatMessage({id: 'experience.beitech.details1'}),
              formatMessage({id: 'experience.beitech.details2'}),
              formatMessage({id: 'experience.beitech.details3'}),
              formatMessage({id: 'experience.beitech.details4'})
            ]}/>

        <ExperienceItem id="zenware" header="Zenware" website="https://zenware.com.co"
            jobTitle={formatMessage({id: 'experience.zenware.jobTitle'})}
            dateRange={formatMessage({id: 'experience.zenware.dateRange'})}
            details={[
              formatMessage({id: 'experience.zenware.details0'}),
              formatMessage({id: 'experience.zenware.details1'}),
              formatMessage({id: 'experience.zenware.details2'})
            ]}/>

        <ExperienceItem id="avantel" header="Avantel" website="http://avantel.co"
            jobTitle={formatMessage({id: 'experience.avantel.jobTitle'})}
            dateRange={formatMessage({id: 'experience.avantel.dateRange'})}
            details={[
              formatMessage({id: 'experience.avantel.details0'})
            ]}/>

        <ExperienceItem id="soluciones360" header="Soluciones 360" website="http://soluciones360.co"
            jobTitle={formatMessage({id: 'experience.soluciones360.jobTitle'})}
            dateRange={formatMessage({id: 'experience.soluciones360.dateRange'})}
            details={[
              formatMessage({id: 'experience.soluciones360.details0'})
            ]}/>

        <ExperienceItem id="envasadorasycentrifugas" header="Envasadoras y Centrifugas" 
            website="https://envasadorasycentrifugas.com"
            jobTitle={formatMessage({id: 'experience.envasadorasycentrifugas.jobTitle'})}
            dateRange={formatMessage({id: 'experience.envasadorasycentrifugas.dateRange'})}
            details={[
              formatMessage({id: 'experience.envasadorasycentrifugas.details0'}),
              formatMessage({id: 'experience.envasadorasycentrifugas.details1'}),
              formatMessage({id: 'experience.envasadorasycentrifugas.details2'}),
              formatMessage({id: 'experience.envasadorasycentrifugas.details3'})
            ]}/>

      </Grid>
    );
  }

}

Experience.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Experience);


