import React from 'react';
import { Col, Row, Panel } from 'react-bootstrap';
import { FormattedMessage } from 'react-intl';

class ExperienceItem extends React.Component {
  render() {
    return (
      <Row>
        <Col xs={12}>
          <Panel>
            <Panel.Body>
              <h2 id={this.props.id}>{this.props.header}</h2>
              <div><strong><FormattedMessage id="experience.item.website"/>:</strong> <a href={this.props.website} target="_blank">{this.props.website}</a></div>
              <div><strong><FormattedMessage id="experience.item.jobTitle"/>:</strong> {this.props.jobTitle}</div>
              <div><strong><FormattedMessage id="experience.item.dateRange"/>:</strong> {this.props.dateRange}</div>
              <div><strong><FormattedMessage id="experience.item.details"/>:</strong></div>
              <ul>
                {this.props.details.map( (detail, index) =>
                  <li key={index}>{detail}</li>
                )}
              </ul>
            </Panel.Body>
          </Panel>
              
        </Col>
      </Row>
    );
  }
}

export default ExperienceItem;


