module.exports = {"header.experience":"Experience","header.services":"Services","header.contact":"Contact","header.language":"Language","home.breadcrumb":"Home","home.jumbotron.header":"Hi, I'm ","home.jumbotron.message":"I'm a web developer. I work as a back-end and front-end developer. I also work with the microservices architecture and practice the DevOps culture.","home.more":"More...","footer.back-to-top":"Back to top","table-of-contents":"Table of contents","experience.breadcrumb":"Experience","experience.header":"Experience","experience.introduction":"I'm a developer of web applications. I have worked with Java for more than 5 years, in which 2 of them were with the Spring Framework. I'm also a follower of the Microservices architecture and the culture of DevOps. I'm currently a holder of Oracle certifications, Oracle Certified Professional Java 8 and Oracle Certified Associate Java 8. I also have a certification for Linux Systems Administration provided by CompTIA and the Linux Professional Institute (LPI). And a certification for developers that work with the different services that AWS provides, AWS Certified Developer - Associate.","experience.item.website":"Website","experience.item.jobTitle":"Job Title","experience.item.dateRange":"Date","experience.item.details":"Details","experience.beitech.jobTitle":"Full Stack Developer and DevOps","experience.beitech.dateRange":"2017-09 - Present","experience.beitech.details0":"Work on the Front-end logic by using AngularJS.","experience.beitech.details1":"Work on the Back-end logic by using Java and the Spring Framework.","experience.beitech.details2":"Automate development environments configurations with Vagrant, shell scripts, and Ansible.","experience.beitech.details3":"Design and implement cloud infrastructure using AWS.","experience.beitech.details4":"Perform general administration of several Linux instances.","experience.zenware.jobTitle":"Full Stack Developer","experience.zenware.dateRange":"2016-03 - 2017-07","experience.zenware.details0":"Perform analysis, design, and implementation of a web application for the management of robotic tournaments. This application executed CRUD operations on the different domain concepts, like for example teams, participants, tournaments, categories, matches, etc. It included responsive design and compatibility with mobile devices and the most common internet browsers. And finally, it had support for the languages English, Spanish, and Japanese.","experience.zenware.details1":"Perform analysis, design, and implementation of the base libraries of all the web projects developed internally, which included best practices and design patterns, protection against the most common attack vectors (security) and a couple of improvements for SEO.","experience.zenware.details2":"Write general purpose bash scripts for the configuration of development and production environments.","experience.avantel.jobTitle":"Java Developer","experience.avantel.dateRange":"2015-03 - 2016-03","experience.avantel.details0":"Add AngularJS to 2 hybrid mobile applications previously implemented with only HTML, CSS, Javascript, and Cordova. This was done in order to make them more easy to maintain introducing a more organized structure that allows reusing components. Also, use of native code for Android and iOS for some requirements. And finally, add unit testing to the REST services that these applications consume.","experience.soluciones360.jobTitle":"Java Developer","experience.soluciones360.dateRange":"2014-07 - 2015-02","experience.soluciones360.details0":"Desktop application with a local database (sqlite), import of data from Excel files, generation of personalized PDF reports, email management, data synchronization through REST services using a custom encryption algorithm (AES key for the bulk of the transmission and RSA for the exchange of the session key). Personalized interface (Custom Look and Feel) with distribution files for Windows (EXE), MAC (DMG APP) and Linux (TARGZ).","experience.envasadorasycentrifugas.jobTitle":"Programmer","experience.envasadorasycentrifugas.dateRange":"2013-01 - 2014-06","experience.envasadorasycentrifugas.details0":"Web application (JSF) with responsive design and dynamic content that allows the creation, edition and deletion of articles detailing the different projects on which the company has worked on in the past.","experience.envasadorasycentrifugas.details1":"Desktop application for payroll management, with Excel reports and printing.","experience.envasadorasycentrifugas.details2":"Automation of sugar centrifugal machines, 6 batch and 3 continuous, using PLCs(S7-200, S7-1200) and HMIs (TP 177B of 6”).","experience.envasadorasycentrifugas.details3":"3D design in Solidworks for different kinds of mechanical devices with its respective drawings for building.","services.breadcrumb":"Services","services.header":"Services","services.introduction":"I like to describe myself as a versatile person because through time I have been performing different roles. I started mostly as a Back-end developer, then I learned Front-end development, and finally learned DevOps. Some people like to use the phrase “Jack of all trades, master of none” when referring to what I do, but, in reality, that is not the case due to that Back-end development is my speciality, and Front-end development and DevOps were things that I learned because I wanted to learn as much as I could.","services.backend.header":"Back-end Developer","services.backend.introduction":"If I were to choose a specialty, then it would be working as a Back-end Developer. This is because in all the projects I’ve been involved my main job was to work on the business logic that ran on the server side and to make sure that the performance and code quality was the best.","services.backend.languages":"When talking about programming languages for the server side that I know, the first language that I happily mention is Java, because I have a great familiarity with it that I gained through all my career, not to mention that I have Oracle certifications. The second language that I want to mention is PHP, because I worked with it for a few months in a project in the past. Finally, the third language that I want to mention is C#, that although I haven’t been able to use it in a professional project, I have familiarity with it.","services.backend.databases":"My main experience with relational databases is with MySQL because I have done a lot of the things a DBA does with it. Apart of the common things like installing and configuring the database server, designing and creating the databases and tables structures, managing users, take and restore backups, monitor the resources used by the database server, the thing I’m most proud of is the tuning of critical queries with the help of the execution plans and the optional restructure of the data and/or implementation of indices in the correct place. Another relational database that I want to mention is Postgres, that although I haven’t done several of the common things a DBA does with it, I can say that I have done some tuning on critical queries. Oracle is another database on which I have worked on in the past, that given my experience with the improvement of queries, it shouldn’t be a problem if I wanted to do the same with it. Additionally, I have some familiarity with the NoSQL databases MongoDB and DynamoDB.","services.backend.scalability":"Another thing that I want to mention is my ability to use different strategies to scale applications depending on the resources available. For example, applications can be scaled by replicating them, by dividing them into smaller parts, by using cache aggressively, by avoiding a state in them, and so on.","services.backend.listIntroduction":"Now, I would like to describe some of the stacks I've used:","services.devops.header":"DevOps Engineer","services.devops.introduction":"Through all my career, I had the opportunity to exercise different abilities that a DevOps has, that allowed me to be more generalist and more useful to the teams I was in.","services.devops.linuxadmin":"In almost all of the project I have worked on, I performed the tasks of a Linux Systems Administrator. First, I installed and configured countless of packages for distributions based on Fedora (Red Hat Enterprise Linux and CentOS) and Debian (Mint, Ubuntu), including their services when they didn’t provide one. Second, I managed users and groups with their respective properties, configured system and application logs, and maintained cron jobs for automated tasks. Third, I configured and troubleshot network interfaces, route tables, and related components, when necessary. Finally, I made a heavy use of shell scripting in order to automate all sort of repetitive tasks, and thanks to this experience and more, I got certified.","services.devops.aws":"In the project I’m currently on, I configured from scratch the architecture used for the applications deployed on AWS. I started by defining the users and their policies that would allow them to access the different services. Then I configured the VPCs with their subnets, security groups, Internet Gateways, Route Tables, and NAT devices for private subnets. After that, I configured several Elastic Beanstalk environments for production, staging, and QA, that included EC2 instances in Auto Balancing groups, and several Application Load Balancers. Of course, I created multiple RDS database instances for each one of the environments. And finally, because all of this infrastructure tends to become even more complex with time, I put it in version control with the help of CloudFormation, that is to say, that I automated the management of this infrastructure. After this experience, I got a certification.","services.devops.vagrantAnsible":"When I began in one of the companies I worked for in the past, I remember that all I had was a bunch of manuals to configure the development environment. The problem with that was that those manuals weren’t up to date anymore since the last modification was done several months ago, and not to mention that not all of the changes to the development environment were documented. So I decided to change that, and opt for a tool like Vagrant, that allowed me to easily \"document\" the configuration in a way that executable and replicable for anyone in any operating system. Originally, I wrote this configuration provision with purely shell scripts, but then I realized that this was hard to maintain due to the limitations of bash. So I integrated Ansible, which made things a lot easier to maintain for me and the other developers. In the end, I used Ansible for any configuration provision that I needed, not only for Vagrant, but for also the AWS EC2 instances, like for example the configuration of the Continuous Integration and Continuous Deployment instance, that included Jenkins and SonarQube.","services.devops.jenkinsSonarQube":"In several projects, I got the opportunity to configure from scratch Linux instances dedicated to Continuous Integration and Continuous Deployment. First, using Ansible, of course, I installed and configured Jenkins with several pipelines for each combination of projects and environments. These pipelines had one step that executed unit tests written with JUnit and several other tools. Second, I installed and configured SonarQube for static code analysis and control.","services.frontend.header":"Front-end Developer","services.frontend.introduction":"Working on web development is not complete without the front end, so that is why I have worked with different technologies and tech stacks during all these years.","services.frontend.traditional":"I started a long time ago with the traditional way of working with front-end technologies, that is pages generated by back-end technologies. First, I used JSP to dynamically generate pages, which is very similar to the way PHP generates pages. Later on, I used JSF with Primefaces when JSP got deprecated. Finally, when the separation of front-end and back-end became more pronounced, I started generating the front-end pages within the browsers making use of Javascript frameworks and consuming web services.","services.frontend.angular":"The first Javascript framework that I used was AngularJS. I integrated this framework on several hybrid mobile applications and a few web applications, although, at that moment there wasn’t a really great support for some Javascript features that forced me to use workarounds for some browsers. The overall result was excellent due to that this framework introduced a better way of keeping things separated and organized that made the source code more maintainable.","services.frontend.react":"Recently, I started learning React to build this web portfolio because it has become a popular alternative to Angular.","contact.breadcrumb":"Contact","contact.header":"Contact","contact.introduction":"It seems you are interested in contacting me, please use my email anfe0690 at gmail dot com or use the following form:","contact.name":"Name","contact.email":"Email","contact.replyto":"Reply To","contact.message":"Message","contact.send":"Send"}

