module.exports = {"header.experience":"Experiencia","header.services":"Servicios","header.contact":"Contacto","header.language":"Lenguaje","home.breadcrumb":"Inicio","home.jumbotron.header":"Hola, soy ","home.jumbotron.message":"Soy un desarrollador de web. Trabajo como un desarrollador back-end y front-end. También trabajo con la arquitectura de microservicios y practico la cultura de DevOps.","home.more":"Mas...","home.summary.experience":"Experiencia","home.summary.aboutme":"Acerca de mi","home.summary.contactme":"Contáctame","footer.back-to-top":"Volver arriba","table-of-contents":"Tabla de contenidos","experience.breadcrumb":"Experiencia","experience.header":"Experiencia","experience.introduction":"Soy un desarrollador de aplicaciones web. He trabajado con Java por mas de 5 años, en los cuales 2 de ellos fueron con el Framework Spring. También soy un seguidor de la arquitectura de microservicios y la cultura de DevOps. Actualmente tengo certificaciones de Oracle, Oracle Certified Professional Java 8 (​<a href=\"https://www.youracclaim.com/badges/23633bfa-a00f-4f89-9aa8-e4ffd6fd54d2/public_url\" target=\"_blank\">Distintivo digital</a>) y Oracle Certified Associate Java 8 (<a href=\"https://www.youracclaim.com/badges/754583e4-99e4-4cef-9774-0752568a90d4/public_url\" target=\"_blank\">Distintivo digital</a>). También tengo una certificación para Linux Systems Administration proveída por CompTIA (<a href=\"https://www.certmetrics.com/comptia/public/verification.aspx?code=TWKMEJXV4R36F29P\" target=\"_blank\">enlace de verificación</a>) y el Linux Professional Institute (LPI), y una certificación para desarrolladores que trabajan con los diferentes servicios que AWS provee, AWS Certified Developer - Associate (<a href=\"http://aws.amazon.com/verification\" target=\"_blank\">enlace de verificación</a> y código: T2N1DSFKJN1QQ0KW).","experience.item.website":"Sitio Web","experience.item.jobTitle":"Nombre del cargo","experience.item.dateRange":"Fecha","experience.item.details":"Detalles","experience.beitech.jobTitle":"Desarrollador Full Stack y DevOps","experience.beitech.dateRange":"2017-09 - Presente","experience.beitech.details0":"Trabajar en la lógica del Front-end usando AngularJS.","experience.beitech.details1":"Trabajar en la lógica del Back-end utilizando Java y Spring Framework.","experience.beitech.details2":"Automatizar configuraciones de entornos de desarrollo con Vagrant, shell scripts y Ansible.","experience.beitech.details3":"Diseñar e implementar infraestructura en la nube utilizando AWS.","experience.beitech.details4":"Realizar administración general de varias instancias de Linux.","experience.zenware.jobTitle":"Desarrollador Java","experience.zenware.dateRange":"2016-03 - 2017-07","experience.zenware.details0":"Realizar análisis, diseño e implementación de una aplicación web para la gestión de torneos de robótica. Esta aplicación ejecutó operaciones CRUD en los diferentes conceptos de dominio, como por ejemplo equipos, participantes, torneos, categorías, partidos, etc. Esta incluyó responsive design y compatibilidad con dispositivos móviles y los navegadores de internet más comunes. Y finalmente, esta tenía soporte para los idiomas inglés, español y japonés.","experience.zenware.details1":"Realizar análisis, diseño e implementación de las librerías base de todos los proyectos web desarrollados internamente, que incluyen mejores prácticas y patrones de diseño, protección contra los vectores de ataque más comunes (seguridad) y varias mejoras para SEO.","experience.zenware.details2":"Escribir scripts bash de propósito general para la configuración de entornos de desarrollo y producción.","experience.avantel.jobTitle":"Desarrollador Full Stack","experience.avantel.dateRange":"2015-03 - 2016-03","experience.avantel.details0":"Agregar AngularJS a 2 aplicaciones móviles híbridas implementadas previamente con solo HTML, CSS, Javascript y Cordova. Esto se hizo para hacerlas más fáciles de mantener introduciendo una estructura más organizada que permita reutilizar componentes. Además, uso de código nativo para Android e iOS para algunos requisitos. Y, por último, agreguar pruebas unitarias a los servicios REST que consumen estas aplicaciones.","experience.soluciones360.jobTitle":"Desarrollador Java","experience.soluciones360.dateRange":"2014-07 - 2015-02","experience.soluciones360.details0":"Aplicación de escritorio con base de datos local (sqlite), importación de datos desde archivos Excel, generación de reportes personalizados en PDF, manejo de correos, sincronización a través servicios REST usando un algoritmo personalizado de cifrado (llave AES para el grueso de la transmisión y RSA para el intercambio de la llave de sesión). Interfaz personalizada (Custom Look and Feel) con distribuciones para Windows (EXE), MAC (DMG APP) y Linux (TARGZ).","experience.envasadorasycentrifugas.jobTitle":"Programador","experience.envasadorasycentrifugas.dateRange":"2013-01 - 2014-06","experience.envasadorasycentrifugas.details0":"Aplicación web (JSF) con diseño responsivo y contenido dinámico que permite la creación, edición y eliminación de artículos detallando los diferentes proyectos en los cuales la compañía ha trabajado en el pasado.","experience.envasadorasycentrifugas.details1":"Aplicación de escritorio para el manejo de nómina, con reportes en Excel e impresiones.","experience.envasadorasycentrifugas.details2":"Automatización de centrifugas de azucar, 6 de tipo batch y 3 de tipo continuas, usando PLCs(S7-200, S7-1200) y HMIs (TP 177B de 6”).","experience.envasadorasycentrifugas.details3":"Diseño 3D en Solidworks para diferentes tipos de dispositivos mecánicos con sus respectivos planos para construcción.","services.breadcrumb":"Servicios","services.header":"Servicios","services.introduction":"Me gusta describirme como una persona versátil porque a través del tiempo he estado desempeñando diferentes roles. Empecé principalmente como desarrollador back-end, luego aprendí desarrollo de front-end y finalmente aprendí DevOps. A algunas personas les gusta usar la frase \"Aprendiz de todo, maestro de nada\" cuando se refiere a lo que hago, pero, en realidad, ese no es el caso debido a que el desarrollo de Back-end es mi especialidad, y el desarrollo de Front-end y DevOps fueron cosas que aprendí porque quería aprender todo lo que podía.","services.backend.header":"Desarrollador Back-end","services.backend.introduction":"Si tuviera que escoger una especialidad, esta sería trabajar como un desarrollador Back-end. Esto es porque en todos los proyectos en los que estuve envuelto mi trabajo principal fue trabajar en la lógica de negocio que corría en el lado del servidor, y asegurar que el rendimiento y la calidad de código fuera la mejor.","services.backend.languages":"Hablando de lenguajes de programación para el lado del servidor que conozco, el primer lenguaje que menciono felizmente es Java, porque tengo una gran familiaridad con el que gané a través de mi carrera, sin mencionar que tengo certificaciones de Oracle. El segundo lenguaje que quiero mencionar es PHP, porque trabajé con él por unos meses en un proyecto en el pasado. Finalmente, el tercer lenguaje que quiero mencionar es C#, que aunque no he podido usarlo en un proyecto profesional, tengo familiaridad con el.","services.backend.databases":"Mi experiencia principal con bases de datos relacionales es con MySQL porque he hecho muchas de las cosas que un DBA hace. Aparte de las cosas comunes como instalar y configurar un servidor de base de datos, diseñar y crear las bases de datos y estructuras de tablas, manejar usuarios, tomar y restaurar backups, monitorear los recursos usados por el servidor de base de datos, la cosa de la que estoy más orgulloso es la puesta a punto de consultas críticas con la ayuda de los planes de ejecución y la reestructura de los datos y/o implementación de índices en el lugar correcto. Otra base de datos relacional que quiero mencionar es Postgres, que aunque no he hecho varias de las cosas que un DBA hace con esta, puedo decir que hecho puesta a punto de consultas críticas. Oracle es otra base de datos con la cual he trabajado en el pasado, que dada mi experiencia con la mejora de consultas, no debería ser un problema si necesitara hacer lo mismo con esta. Adicionalmente, tengo algo de familiaridad con las bases de datos NoSQL MongoDB y DynamoDB.","services.backend.scalability":"Otra cosa que deseo mencionar es mi habilidad para usar estrategias diferentes para escalar aplicaciones dependiendo de los recursos disponibles. Por ejemplo, las aplicaciones pueden ser escaladas al replicarlas, al dividirlas en partes pequeñas, al usar caché agresivamente, al evitar el uso de estado en ellas, y demás.","services.backend.listIntroduction":"Ahora me gustaría mostrar algunos de los stacks que he usado:","services.devops.header":"Ingeniero DevOps","services.devops.introduction":"A lo largo de toda mi carrera, tuve la oportunidad de ejercitar diferentes habilidades que tiene un DevOps, que me permitieron ser más generalista y más útil para los equipos en los que estaba.","services.devops.linuxadmin":"En casi todo proyecto en el que he trabajado, realicé las tareas de un administrador de sistemas Linux. Primero, instalé y configuré innumerables paquetes para distribuciones basadas en Fedora (Red Hat Enterprise Linux y CentOS) y Debian (Mint, Ubuntu), incluidos sus servicios cuando no proporcionaban uno. En segundo lugar, administré usuarios y grupos con sus respectivas propiedades, configuré logs del sistema y de aplicaciones, y mantuve cron jobs para tareas automatizadas. Tercero, configuré y solucioné problemas de interfaces de red, tablas de rutas y componentes relacionados, cuando fue necesario. Finalmente, hice uso intensivo de shell scripts para automatizar todo tipo de tareas repetitivas, y gracias a esta experiencia y más, me certifiqué.","services.devops.aws":"En el proyecto en el que estoy actualmente, configuré desde cero la arquitectura utilizada para las aplicaciones desplegadas en AWS. Empecé definiendo los usuarios y sus políticas que les permitirían acceder a los diferentes servicios. Luego configuré las VPCs con sus subredes, grupos de seguridad, puertas de enlace de Internet, tablas de rutas e incluso dispositivos NAT para subredes privadas. Después de eso, configuré varios entornos Elastic Beanstalk para producción, staging y QA, que incluían instancias EC2 en Auto Balancing Groups y varios Application Load Balancers. Por supuesto, se crearon varias instancias de base de datos RDS para cada uno de los entornos. Y finalmente, debido a que toda esta infraestructura tiende a volverse aún más compleja con el tiempo, la puse en control de versiones con la ayuda de CloudFormation, es decir, automaticé la administración de esta infraestructura. También obtuve una certificación después de esta experiencia.","services.devops.vagrantAnsible":"Cuando comencé en una de las empresas para la que trabajé en el pasado, recuerdo que todo lo que tenía era una serie de manuales para configurar el entorno de desarrollo. El problema era que esos manuales ya no estaban actualizados, ya que la última modificación se realizó meses atrás, sin mencionar que no todos los cambios en el entorno de desarrollo estaban documentados. Así que decidí cambiar eso y optar por una herramienta como Vagrant, que me permitió \"documentar\" fácilmente la configuración de una manera que sea ejecutable y replicable para cualquier persona en cualquier sistema operativo. Originalmente escribí este aprovisionamiento de configuración con sólamente shell scripts, pero luego me di cuenta de que esto era difícil de mantener debido a las limitaciones de bash. Así que integré Ansible, lo que hizo que las cosas fueran mucho más fáciles de mantener para mí y los otros desarrolladores. Al final, utilicé Ansible para cualquier aprovisionamiento de configuración que necesitaba, no solo para Vagrant, sino también para las instancias de AWS EC2, como por ejemplo la configuración de la instancia de Integración Continua y Despliegue Continuo, que incluía Jenkins y SonarQube.","services.devops.jenkinsSonarQube":"En varios proyectos, tuve la oportunidad de configurar desde cero instancias de Linux dedicadas para la Integración Continua y Despliegue Continuo. Primero, por supuesto usando Ansible, instalé y configuré Jenkins con varias Pipelines para cada combinación de proyectos y entornos. Estos Pipelines tenían un paso en que ejecutaba pruebas unitarias escritas con JUnit y varias otras herramientas. En segundo lugar, instalé y configuré SonarQube para análisis y control de código estático.","services.frontend.header":"Desarrollador Front-end","services.frontend.introduction":"Trabajar en desarrollo web no está completo sin el front-end, es por esto que he trabajado con diferentes tecnologías durante todos estos años.","services.frontend.traditional":"Empecé hace mucho tiempo con la forma tradicional de trabajar con tecnologías de front-end, es decir, páginas generadas por tecnologías de back-end. Primero, utilicé JSP para generar dinámicamente páginas, que es muy similar a la forma en que PHP genera páginas. Más tarde, utilicé JSF con Primefaces cuando JSP quedó en desuso. Finalmente, cuando la separación entre el front-end y el back-end se hizo más pronunciada, comencé a generar las páginas front-end dentro de los navegadores utilizando frameworks de JavaScript y consumiendo servicios web.","services.frontend.angular":"El primer framework de JavaScript que utilicé fue AngularJS. Integré este framework en varias aplicaciones móviles híbridas y algunas aplicaciones web, aunque, en ese momento, no había un gran soporte para algunas características de Javascript que me obligaban a utilizar soluciones temporales para algunos navegadores. El resultado general fue excelente debido a que este marco introdujo una mejor manera de mantener las cosas separadas y organizadas que hicieron que el código fuente fuera más fácil de mantener.","services.frontend.react":"Recientemente, comencé a aprender React para construir este portafolio web porque se ha convertido en una alternativa popular a Angular.","contact.breadcrumb":"Contacto","contact.header":"Contacto","contact.introduction":"Parece que estás interesado en contactarme, por favor usa mi correo electrónico anfe0690 en gmail punto com o usa el siguiente formulario:","contact.name":"Nombre","contact.email":"Correo electrónico","contact.replyto":"Responder a","contact.message":"Mensaje","contact.send":"Enviar"}

