import 'common/footer/Footer.css';

import React from 'react';
import { Glyphicon, Grid, Row, Col } from 'react-bootstrap';
import { FormattedMessage } from 'react-intl';

class Footer extends React.Component {

  backToTopHandleClick() {
    document.body.scrollTop = 0; // For Safari
    document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
  }

  render() {
    return (
      <footer className="page-footer">
        <Grid>
          <Row>
            <Col xs={12}>
              <button className="btn btn-default" type="button" onClick={this.backToTopHandleClick}><Glyphicon glyph="chevron-up" /> <FormattedMessage id="footer.back-to-top"/></button>
            </Col>
          </Row>
        </Grid>
      </footer>
    );
  }
}

export default Footer;


