import 'services/Services.css';
import oca from 'img/oca.png';
import ocp from 'img/ocp.png';
import comptiaLinux from 'img/comptia-linux.png';
import awsLogo from 'img/aws-logo.png';
import awsTag from 'img/aws-tag.png';

import React from 'react';
import { Grid, Row, Col, Breadcrumb, PageHeader } from 'react-bootstrap';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';

import TableOfContents from 'common/table-of-contents/TableOfContents';

class Services extends React.Component {

  componentDidMount() {
    document.title = this.props.intl.formatMessage({id: "services.header"}) + " - Andres Muñoz";
  }

  render() {
    const formatMessage = this.props.intl.formatMessage;

    return (
      <Grid>

        {/* Breadcrumb */}
        <Row>
          <Col xs={12}>
            <Breadcrumb>
              <Breadcrumb.Item href="/"><FormattedMessage id="home.breadcrumb"/></Breadcrumb.Item>
              <Breadcrumb.Item active><FormattedMessage id="services.breadcrumb"/></Breadcrumb.Item>
            </Breadcrumb>
          </Col>
        </Row>

        {/* Introduction */}
        <Row>
          <Col xs={12}>
            <PageHeader><FormattedMessage id="services.header"/></PageHeader>
            <p><FormattedMessage id="services.introduction"/></p>
          </Col>
        </Row>

        <TableOfContents items={[
          { hash: 'backend', name: formatMessage({id: 'services.backend.header'}) },
          { hash: 'devops', name: formatMessage({id: 'services.devops.header'}) },
          { hash: 'frontend', name: formatMessage({id: 'services.frontend.header'}) }
        ]}/>

        {/* Back-end Developer */}
        <Row>
          <Col xs={12}>
            <h2 id="backend">{formatMessage({id:'services.backend.header'})}</h2>
            <p><FormattedMessage id="services.backend.introduction"/></p>
            <p><FormattedMessage id="services.backend.languages"/></p>
            <p className="oc-content"><img src={oca} alt="oca" className="oc"/><img src={ocp} alt="ocp" className="oc"/></p>
            <p><FormattedMessage id="services.backend.databases"/></p>
            <p><FormattedMessage id="services.backend.scalability"/></p>
            <p><FormattedMessage id="services.backend.listIntroduction"/></p>
          </Col>
        </Row>
        <Row>
          <Col xs={12} sm={6} md={4}>
            <ul>
              <li>Java 8</li>
              <li>Git</li>
              <li>Maven</li>
              <li>JUnit, Hamcrest, JaCoCo, Mockito, Selenium</li>
              <li>Spring Framework</li>
              <li>Tomcat, Apache HTTP</li>
              <li>MySQL</li>
              <li>Jenkins, SonarQube</li>
              <li>AWS (Elastic Beanstalk, VPC, S3, EC2, RDS, DynamoDB, CloudFormation)</li>
              <li>Linux (Amazon Linux, CentOS, Fedora)</li>
              <li>Ansible</li>
            </ul>
          </Col>
          <Col xs={12} sm={6} md={4}>
            <ul>
              <li>Java 8</li>
              <li>Git</li>
              <li>Maven</li>
              <li>TestNG, Hamcrest, JaCoCo, Mockito, Selenium</li>
              <li>JEE (JSF, EJB, JPA)</li>
              <li>Wildfly, Apache HTTP</li>
              <li>PostgreSQL</li>
              <li>MongoDB</li>
            </ul>
          </Col>
          <Col xs={12} sm={6} md={4}>
            <ul>
              <li>PHP 5.6</li>
              <li>Git</li>
              <li>PHPUnit</li>
              <li>Codeigniter</li>
              <li>Apache HTTP</li>
              <li>MySQL</li>
              <li>Jenkins</li>
              <li>Vagrant</li>
            </ul>
          </Col>
        </Row>

        {/* DevOps Engineer */}
        <Row>
          <Col xs={12}>
            <h2 id="devops">{formatMessage({id:'services.devops.header'})}</h2>
            <p><FormattedMessage id="services.devops.introduction"/></p>
            <p><FormattedMessage id="services.devops.linuxadmin"/></p>
            <p className="text-center certificate"><img src={comptiaLinux} alt="Comptia Linux+" className="certificate comptia-linux"/></p>
            <p><FormattedMessage id="services.devops.aws"/></p>
            <p className="text-center certificate">
              <img src={awsLogo} alt="AWS Logo" className="aws-logo"/>
              <br/>
              <img src={awsTag} alt="AWS Tag" className="aws-tag"/>
            </p>
            <p><FormattedMessage id="services.devops.vagrantAnsible"/></p>
            <p><FormattedMessage id="services.devops.jenkinsSonarQube"/></p>
          </Col>
        </Row>

        {/* Front-end Developer */}
        <Row>
          <Col xs={12}>
            <h2 id="frontend">{formatMessage({id:'services.frontend.header'})}</h2>
            <p><FormattedMessage id="services.frontend.introduction"/></p>
            <p><FormattedMessage id="services.frontend.traditional"/></p>
            <p><FormattedMessage id="services.frontend.angular"/></p>
            <p><FormattedMessage id="services.frontend.react"/></p>
          </Col>
        </Row>

      </Grid>
    );
  }
}

Services.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Services);


