import 'contact/Contact.css';

import React from 'react';
import { Grid, Row, Col, Breadcrumb, PageHeader, FormGroup, ControlLabel, FormControl } from 'react-bootstrap';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';

class Contact extends React.Component {

  componentDidMount() {
    document.title = this.props.intl.formatMessage({id: "contact.header"}) + " - Andres Muñoz";
  }

  render() {
    const formatMessage = this.props.intl.formatMessage;

    return (
      <Grid>

        {/* Breadcrumb */}
        <Row>
          <Col xs={12}>
            <Breadcrumb>
              <Breadcrumb.Item href="/"><FormattedMessage id="home.breadcrumb"/></Breadcrumb.Item>
              <Breadcrumb.Item active><FormattedMessage id="contact.breadcrumb"/></Breadcrumb.Item>
            </Breadcrumb>
          </Col>
        </Row>

        {/* Introduction */}
        <Row>
          <Col xs={12}>
            <PageHeader><FormattedMessage id="contact.header"/></PageHeader>
            <p><FormattedMessage id="contact.introduction"/></p>
          </Col>
        </Row>

        {/* Email form */}
        <Row>
          <Col xs={12} mdOffset={3} md={6}>
            <form action="https://formspree.io/anfe0690@gmail.com" method="POST">
              <input type="hidden" name="_subject" value="Web Portfolio" />
              <input type="text" name="_gotcha" />
              <input type="hidden" name="_next" value={window.location.href} />
              <FormGroup>
                <ControlLabel><FormattedMessage id="contact.name"/></ControlLabel>
                <FormControl type="text" name="name" placeholder={formatMessage({id:'contact.name'})} required/>
              </FormGroup>
              <FormGroup>
                <ControlLabel><FormattedMessage id="contact.email"/></ControlLabel>
                <FormControl type="email" name="_replyto" placeholder={formatMessage({id:'contact.email'})} required/>
              </FormGroup>
              <FormGroup>
                <ControlLabel><FormattedMessage id="contact.message"/></ControlLabel>
                <FormControl componentClass="textarea" name="message" placeholder={formatMessage({id:'contact.message'})} rows="8" required/>
              </FormGroup>
              <FormControl type="submit" value={formatMessage({id:'contact.send'})} className="btn btn-primary"/>
            </form>
          </Col>
        </Row>

      </Grid>
    );
  }
}

Contact.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Contact);


