import logo from 'logo.svg'

import React from 'react';
import { Navbar, Nav, NavItem, NavDropdown, MenuItem } from 'react-bootstrap';
import FlagIcon from 'common/FlagIcon';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faGlobe from '@fortawesome/fontawesome-free-solid/faGlobe';

class Header extends React.Component {

  constructor(props) {
    super(props);
    this.onLanguageChanged = this.onLanguageChanged.bind(this);
  }

  onLanguageChanged(language) {
    this.props.onLanguageChanged(language);
  }

  render() {
    const titleLabel = this.props.intl.formatMessage({id: 'header.language'});
    const title = <span><FontAwesomeIcon icon={faGlobe} /> {titleLabel}</span>;

    return (
      <Navbar fixedTop={true}>
        <Navbar.Header>
        <Navbar.Brand>
          <a className="navbar-brand" href="/">
            <img src={logo} alt="Logo" className="logo" />
          </a>
        </Navbar.Brand>
        <Navbar.Toggle/>
        </Navbar.Header>
        <Navbar.Collapse>
        <Nav>
          <NavItem eventKey={1} active={window.location.pathname === '/experience'} href="/experience"><FormattedMessage id="header.experience"/></NavItem>
          <NavItem eventKey={3} active={window.location.pathname === '/services'} href="/services"><FormattedMessage id="header.services"/></NavItem>
          <NavItem eventKey={7} active={window.location.pathname === '/contact'} href="/contact"><FormattedMessage id="header.contact"/></NavItem>
        </Nav>
        <Nav pullRight>
          <NavDropdown eventKey={8} title={title} id="language-dropdown">
            <MenuItem eventKey={8.1} onClick={this.onLanguageChanged.bind(this, 'en')}><FlagIcon code="gb"/> English</MenuItem>
            <MenuItem eventKey={8.2} onClick={this.onLanguageChanged.bind(this, 'es')}><FlagIcon code="es"/> Español</MenuItem>
          </NavDropdown>
        </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }

}

Header.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Header);


