import React from 'react';
import { Panel, ListGroup, ListGroupItem, Row, Col } from 'react-bootstrap';
import { FormattedMessage } from 'react-intl';

class TableOfContents extends React.Component {

  render () {
    return(
      <Row>
        <Col xs={12} md={4}>
          <Panel>
            <Panel.Heading><FormattedMessage id="table-of-contents"/></Panel.Heading>
            <ListGroup>
              {this.props.items.map( (item, index)=>
                <ListGroupItem key={index} href={'#' + item.hash}>{index + 1 + '. ' + item.name}</ListGroupItem>
              )}
            </ListGroup>
          </Panel>
        </Col>
      </Row>
    );
  }

}

export default TableOfContents;


