import 'home/Home.css';
import logo from 'logo.svg'

import React from 'react';
import { Breadcrumb, Grid, Row, Col, Jumbotron, Panel } from 'react-bootstrap';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';

class Home extends React.Component {

  componentDidMount() {
    document.title = "Andres Muñoz";
  }

  render() {
    return (
      <Grid>

        {/* Breadcrumb */}
        <Row>
          <Col xs={12}>
            <Breadcrumb>
              <Breadcrumb.Item active><FormattedMessage id="home.breadcrumb"/></Breadcrumb.Item>
            </Breadcrumb>
          </Col>
        </Row>

        {/* Jumbotron */}
        <Row>
          <Col xs={12}>
            <Jumbotron>
              <h1 className="main-color">
                <FormattedHTMLMessage 
                  id="home.jumbotron.header"
                  values={{
                    logo: logo
                  }}/>
                  <img src={logo} alt="Logo" className="logo"/>ndres.
              </h1>
              <p><FormattedMessage id="home.jumbotron.message"/></p>
              <p></p>
            </Jumbotron>
          </Col>
        </Row>

        <Row>

          {/* Experience */}
          <Col xs={12} md={8}>
            <Panel>
              <Panel.Heading><FormattedMessage id="experience.header"/></Panel.Heading>
              <Panel.Body>
                <div><FormattedHTMLMessage id="experience.introduction"/></div>
                <div className="more-button">
                  <a className="btn btn-default" href="/experience" role="button">
                    <FormattedMessage id="home.more"/>
                  </a>
                </div>
              </Panel.Body>
            </Panel>
          </Col>

          {/* Services - Back-end developer */}
          <Col xs={12} md={4}>
            <Panel>
              <Panel.Heading><FormattedMessage id="services.backend.header"/></Panel.Heading>
              <Panel.Body>
                <div><FormattedMessage id="services.backend.introduction"/></div>
                <div className="more-button">
                  <a className="btn btn-default" href="/services#backend" role="button">
                    <FormattedMessage id="home.more"/>
                  </a>
                </div>
              </Panel.Body>
            </Panel>
          </Col>

        </Row>

        <Row>
          {/* Services - DevOps engineer */}
          <Col xs={12} md={4}>
            <Panel>
              <Panel.Heading><FormattedMessage id="services.devops.header"/></Panel.Heading>
              <Panel.Body>
                <div><FormattedMessage id="services.devops.introduction"/></div>
                <div className="more-button">
                  <a className="btn btn-default" href="/services#devops" role="button">
                    <FormattedMessage id="home.more"/>
                  </a>
                </div>
              </Panel.Body>
            </Panel>
          </Col>
          {/* Services - Front-end developer */}
          <Col xs={12} md={4}>
            <Panel>
              <Panel.Heading><FormattedMessage id="services.frontend.header"/></Panel.Heading>
              <Panel.Body>
                <div><FormattedMessage id="services.frontend.introduction"/></div>
                <div className="more-button">
                  <a className="btn btn-default" href="/services#frontend" role="button">
                    <FormattedMessage id="home.more"/>
                  </a>
                </div>
              </Panel.Body>
            </Panel>
          </Col>
        </Row>

      </Grid>
    );
  }
}

export default Home;


